<template>
  <div id="serviceInfo">
    <div class="header">
      <div class="van-hairline--bottom van-nav-bar">
        <div class="van-nav-bar__left">
          <i class="van-icon van-icon-arrow van-nav-bar__arrow" @click="$router.back(-1)" style="color: #000000 !important;"></i>
        </div>
        <div class="van-ellipsis van-nav-bar__title">维修详情</div>
      </div>
    </div>
    <div class="login_box">
      <div class="background_color_ffffff">
        <div style="display: inline-block;height: 50px;width: 18%" class="text_center">
          <img :src="serviceInfo.user.userimg" width="40px" style="margin-top: 5px;">
        </div>
        <div style="display: inline-block;height: 40px;width: 78%;vertical-align: bottom;">
          <span>
            <span class="font_14 color_8b8b8b">{{serviceInfo.user.username}}</span>
            <span class="float_right font_12">{{serviceInfo.tell}}</span>
          </span>
          <br>
          <span class="font_12">
            <img src="static/img/vipp.png"/>
            <span class="font_12 color_fcba62">会员</span>
            <span class="float_right color_8b8b8b">发布时间:{{serviceInfo.addtime}}</span>
          </span>
        </div>
        <div style="padding: 5px 10px">
          <div class="padding_top_bottom_5 font_14">{{serviceInfo.title}}</div>
          <div class="font_12 color_8b8b8b line_height_16 padding_top_bottom_5">
            {{serviceInfo.content}}
          </div>
          <div class="login_bar background_color_ffffff login_top padding_top_bottom_5" v-if="serviceInfo.sameimg">
            <img :src="item" class="img_service" v-for="(item,index) in serviceInfo.sameimg.split(',')"/>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'serviceInfo',
    data() {
      return {
        userId:localStorage.getItem('userId'),
        samecityId:'',
        serviceInfo:[],
      }
    },
    mounted() {
      console.log(this.$store.state.serviceInfo);
      this.samecityId = this.$store.state.serviceInfo.samecityid;
      this.getServiceInfo();
    },
    methods:{
      getServiceInfo(){
        let $this = this;
        let obj = {
          userid: $this.userId,
          samecityid:$this.samecityId,
        };
        let qs = require('qs');
        $this.$ajax.post("samecity/getsamecityinfo", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.serviceInfo = rpdata.data.data;
          }
        })
        .catch(function(err) {})
      },
    }
  }
</script>
<style scoped>
  .img_service{
    width: 32.3%;
    padding: 1px;
  }
  .login_box{
    margin-top: 50px;
  }
</style>



